import React, { Component } from 'react'
import { Link } from 'gatsby'
import Layout from '../components/layout'
import ProjectsController from './ProjectsController'
import * as THREE from 'three'
import { MTLLoader, OBJLoader } from 'three-obj-mtl-loader'
import * as basicScroll from 'basicscroll'

/**
 * @author alteredq / http://alteredqualia.com/
 *
 * Full-screen textured quad shader
 */

THREE.CopyShader = {
  uniforms: {
    tDiffuse: { value: null },
    opacity: { value: 1.0 },
  },

  vertexShader: [
    'varying vec2 vUv;',

    'void main() {',

    'vUv = uv;',
    'gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );',

    '}',
  ].join('\n'),

  fragmentShader: [
    'uniform float opacity;',

    'uniform sampler2D tDiffuse;',

    'varying vec2 vUv;',

    'void main() {',

    'vec4 texel = texture2D( tDiffuse, vUv );',
    'gl_FragColor = opacity * texel;',

    '}',
  ].join('\n'),
}

/**
 * @author felixturner / http://airtight.cc/
 *
 * RGB Shift Shader
 * Shifts red and blue channels from center in opposite directions
 * Ported from http://kriss.cx/tom/2009/05/rgb-shift/
 * by Tom Butterworth / http://kriss.cx/tom/
 *
 * amount: shift distance (1 is width of input)
 * angle: shift angle in radians
 */

THREE.DigitalGlitch = {
  uniforms: {
    tDiffuse: { value: null }, //diffuse texture
    tDisp: { value: null }, //displacement texture for digital glitch squares
    byp: { value: 0 }, //apply the glitch ?
    amount: { value: 0.08 },
    angle: { value: 0.02 },
    seed: { value: 0.02 },
    seed_x: { value: 0.02 }, //-1,1
    seed_y: { value: 0.02 }, //-1,1
    distortion_x: { value: 0.5 },
    distortion_y: { value: 0.6 },
    col_s: { value: 0.05 },
  },

  vertexShader: [
    'varying vec2 vUv;',
    'void main() {',
    'vUv = uv;',
    'gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );',
    '}',
  ].join('\n'),

  fragmentShader: [
    'uniform int byp;', //should we apply the glitch ?

    'uniform sampler2D tDiffuse;',
    'uniform sampler2D tDisp;',

    'uniform float amount;',
    'uniform float angle;',
    'uniform float seed;',
    'uniform float seed_x;',
    'uniform float seed_y;',
    'uniform float distortion_x;',
    'uniform float distortion_y;',
    'uniform float col_s;',

    'varying vec2 vUv;',

    'float rand(vec2 co){',
    'return fract(sin(dot(co.xy ,vec2(12.9898,78.233))) * 43758.5453);',
    '}',

    'void main() {',
    'if(byp<1) {',
    'vec2 p = vUv;',
    'float xs = floor(gl_FragCoord.x / 0.5);',
    'float ys = floor(gl_FragCoord.y / 0.5);',
    //based on staffantans glitch shader for unity https://github.com/staffantan/unityglitch
    'vec4 normal = texture2D (tDisp, p*seed*seed);',
    'if(p.y<distortion_x+col_s && p.y>distortion_x-col_s*seed) {',
    'if(seed_x>0.){',
    'p.y = 1. - (p.y + distortion_y);',
    '}',
    'else {',
    'p.y = distortion_y;',
    '}',
    '}',
    'if(p.x<distortion_y+col_s && p.x>distortion_y-col_s*seed) {',
    'if(seed_y>0.){',
    'p.x=distortion_x;',
    '}',
    'else {',
    'p.x = 1. - (p.x + distortion_x);',
    '}',
    '}',
    'p.x+=normal.x*seed_x*(seed/5.);',
    'p.y+=normal.y*seed_y*(seed/5.);',
    //base from RGB shift shader
    'vec2 offset = amount * vec2( cos(angle), sin(angle));',
    'vec4 cr = texture2D(tDiffuse, p + offset);',
    'vec4 cga = texture2D(tDiffuse, p);',
    'vec4 cb = texture2D(tDiffuse, p - offset);',
    'gl_FragColor = vec4(cr.r, cga.g, cb.b, cga.a);',
    //add noise
    'vec4 snow = 200.*amount*vec4(rand(vec2(xs * seed,ys * seed*50.))*0.2);',
    'gl_FragColor = gl_FragColor+ snow;',
    '}',
    'else {',
    'gl_FragColor=texture2D (tDiffuse, vUv);',
    '}',
    '}',
  ].join('\n'),
}

/**
 * @author alteredq / http://alteredqualia.com/
 */

THREE.EffectComposer = function(renderer, renderTarget) {
  this.renderer = renderer

  if (renderTarget === undefined) {
    var parameters = {
      minFilter: THREE.LinearFilter,
      magFilter: THREE.LinearFilter,
      format: THREE.RGBAFormat,
      stencilBuffer: false,
    }

    var size = renderer.getDrawingBufferSize()
    renderTarget = new THREE.WebGLRenderTarget(
      size.width,
      size.height,
      parameters
    )
    renderTarget.texture.name = 'EffectComposer.rt1'
  }

  this.renderTarget1 = renderTarget
  this.renderTarget2 = renderTarget.clone()
  this.renderTarget2.texture.name = 'EffectComposer.rt2'

  this.writeBuffer = this.renderTarget1
  this.readBuffer = this.renderTarget2

  this.passes = []

  // dependencies

  if (THREE.CopyShader === undefined) {
    console.error('THREE.EffectComposer relies on THREE.CopyShader')
  }

  if (THREE.ShaderPass === undefined) {
    console.error('THREE.EffectComposer relies on THREE.ShaderPass')
  }

  this.copyPass = new THREE.ShaderPass(THREE.CopyShader)
}

Object.assign(THREE.EffectComposer.prototype, {
  swapBuffers: function() {
    var tmp = this.readBuffer
    this.readBuffer = this.writeBuffer
    this.writeBuffer = tmp
  },

  addPass: function(pass) {
    this.passes.push(pass)

    var size = this.renderer.getDrawingBufferSize()
    pass.setSize(size.width, size.height)
  },

  insertPass: function(pass, index) {
    this.passes.splice(index, 0, pass)
  },

  render: function(delta) {
    var maskActive = false

    var pass,
      i,
      il = this.passes.length

    for (i = 0; i < il; i++) {
      pass = this.passes[i]

      if (pass.enabled === false) continue

      pass.render(
        this.renderer,
        this.writeBuffer,
        this.readBuffer,
        delta,
        maskActive
      )

      if (pass.needsSwap) {
        if (maskActive) {
          var context = this.renderer.context

          context.stencilFunc(context.NOTEQUAL, 1, 0xffffffff)

          this.copyPass.render(
            this.renderer,
            this.writeBuffer,
            this.readBuffer,
            delta
          )

          context.stencilFunc(context.EQUAL, 1, 0xffffffff)
        }

        this.swapBuffers()
      }

      if (THREE.MaskPass !== undefined) {
        if (pass instanceof THREE.MaskPass) {
          maskActive = true
        } else if (pass instanceof THREE.ClearMaskPass) {
          maskActive = false
        }
      }
    }
  },

  reset: function(renderTarget) {
    if (renderTarget === undefined) {
      var size = this.renderer.getDrawingBufferSize()

      renderTarget = this.renderTarget1.clone()
      renderTarget.setSize(size.width, size.height)
    }

    this.renderTarget1.dispose()
    this.renderTarget2.dispose()
    this.renderTarget1 = renderTarget
    this.renderTarget2 = renderTarget.clone()

    this.writeBuffer = this.renderTarget1
    this.readBuffer = this.renderTarget2
  },

  setSize: function(width, height) {
    this.renderTarget1.setSize(width, height)
    this.renderTarget2.setSize(width, height)

    for (var i = 0; i < this.passes.length; i++) {
      this.passes[i].setSize(width, height)
    }
  },
})

THREE.Pass = function() {
  // if set to true, the pass is processed by the composer
  this.enabled = true

  // if set to true, the pass indicates to swap read and write buffer after rendering
  this.needsSwap = true

  // if set to true, the pass clears its buffer before rendering
  this.clear = false

  // if set to true, the result of the pass is rendered to screen
  this.renderToScreen = false
}

Object.assign(THREE.Pass.prototype, {
  setSize: function(width, height) {},

  render: function(renderer, writeBuffer, readBuffer, delta, maskActive) {
    console.error('THREE.Pass: .render() must be implemented in derived pass.')
  },
})

/**
 * @author alteredq / http://alteredqualia.com/
 */

THREE.RenderPass = function(
  scene,
  camera,
  overrideMaterial,
  clearColor,
  clearAlpha
) {
  THREE.Pass.call(this)

  this.scene = scene
  this.camera = camera

  this.overrideMaterial = overrideMaterial

  this.clearColor = clearColor
  this.clearAlpha = clearAlpha !== undefined ? clearAlpha : 0

  this.clear = true
  this.clearDepth = false
  this.needsSwap = false
}

THREE.RenderPass.prototype = Object.assign(
  Object.create(THREE.Pass.prototype),
  {
    constructor: THREE.RenderPass,

    render: function(renderer, writeBuffer, readBuffer, delta, maskActive) {
      var oldAutoClear = renderer.autoClear
      renderer.autoClear = false

      this.scene.overrideMaterial = this.overrideMaterial

      var oldClearColor, oldClearAlpha

      if (this.clearColor) {
        oldClearColor = renderer.getClearColor().getHex()
        oldClearAlpha = renderer.getClearAlpha()

        renderer.setClearColor(this.clearColor, this.clearAlpha)
      }

      if (this.clearDepth) {
        renderer.clearDepth()
      }

      renderer.render(
        this.scene,
        this.camera,
        this.renderToScreen ? null : readBuffer,
        this.clear
      )

      if (this.clearColor) {
        renderer.setClearColor(oldClearColor, oldClearAlpha)
      }

      this.scene.overrideMaterial = null
      renderer.autoClear = oldAutoClear
    },
  }
)

/**
 * @author alteredq / http://alteredqualia.com/
 */

THREE.GlitchPass = function(dt_size) {
  THREE.Pass.call(this)

  if (THREE.DigitalGlitch === undefined)
    console.error('THREE.GlitchPass relies on THREE.DigitalGlitch')

  var shader = THREE.DigitalGlitch
  this.uniforms = THREE.UniformsUtils.clone(shader.uniforms)

  if (dt_size == undefined) dt_size = 64

  this.uniforms['tDisp'].value = this.generateHeightmap(dt_size)

  this.material = new THREE.ShaderMaterial({
    uniforms: this.uniforms,
    vertexShader: shader.vertexShader,
    fragmentShader: shader.fragmentShader,
  })

  this.camera = new THREE.OrthographicCamera(-1, 1, 1, -1, 0, 1)
  this.scene = new THREE.Scene()

  this.quad = new THREE.Mesh(new THREE.PlaneBufferGeometry(2, 2), null)
  this.quad.frustumCulled = false // Avoid getting clipped
  this.scene.add(this.quad)

  this.goWild = false
  this.curF = 0
  this.generateTrigger()
}

THREE.GlitchPass.prototype = Object.assign(
  Object.create(THREE.Pass.prototype),
  {
    constructor: THREE.GlitchPass,

    render: function(renderer, writeBuffer, readBuffer, delta, maskActive) {
      this.uniforms['tDiffuse'].value = readBuffer.texture
      this.uniforms['seed'].value = Math.random() //default seeding
      this.uniforms['byp'].value = 1

      if (this.goWild === true) {
        this.uniforms['byp'].value = 0
        this.uniforms['amount'].value = Math.random() / 90
        this.uniforms['angle'].value = THREE.Math.randFloat(-Math.PI, Math.PI)
        this.uniforms['distortion_x'].value = THREE.Math.randFloat(0, 1)
        this.uniforms['distortion_y'].value = THREE.Math.randFloat(0, 1)
        this.uniforms['seed_x'].value = THREE.Math.randFloat(-0.3, 0.3)
        this.uniforms['seed_y'].value = THREE.Math.randFloat(-0.3, 0.3)
      }

      // this.uniforms[ "tDiffuse" ].value = readBuffer.texture;
      // this.uniforms[ 'seed' ].value = Math.random(); //default seeding
      // this.uniforms[ 'byp' ].value = 0;

      // if ( this.curF % this.randX == 0 || this.goWild == true ) {

      // 	this.uniforms[ 'amount' ].value = Math.random() / 30;
      // 	this.uniforms[ 'angle' ].value = THREE.Math.randFloat( - Math.PI, Math.PI );
      // 	this.uniforms[ 'seed_x' ].value = THREE.Math.randFloat( - 1, 1 );
      // 	this.uniforms[ 'seed_y' ].value = THREE.Math.randFloat( - 1, 1 );
      // 	this.uniforms[ 'distortion_x' ].value = THREE.Math.randFloat( 0, 1 );
      // 	this.uniforms[ 'distortion_y' ].value = THREE.Math.randFloat( 0, 1 );
      // 	this.curF = 0;
      // 	this.generateTrigger();

      // } else if ( this.curF % this.randX < this.randX / 5 ) {

      // 	this.uniforms[ 'amount' ].value = Math.random() / 90;
      // 	this.uniforms[ 'angle' ].value = THREE.Math.randFloat( - Math.PI, Math.PI );
      // 	this.uniforms[ 'distortion_x' ].value = THREE.Math.randFloat( 0, 1 );
      // 	this.uniforms[ 'distortion_y' ].value = THREE.Math.randFloat( 0, 1 );
      // 	this.uniforms[ 'seed_x' ].value = THREE.Math.randFloat( - 0.3, 0.3 );
      // 	this.uniforms[ 'seed_y' ].value = THREE.Math.randFloat( - 0.3, 0.3 );

      // } else if ( this.goWild == false ) {

      // 	this.uniforms[ 'byp' ].value = 1;

      // }

      this.curF++
      this.quad.material = this.material

      if (this.renderToScreen) {
        renderer.render(this.scene, this.camera)
      } else {
        renderer.render(this.scene, this.camera, writeBuffer, this.clear)
      }
    },

    generateTrigger: function() {
      this.randX = THREE.Math.randInt(120, 240)
    },

    generateHeightmap: function(dt_size) {
      var data_arr = new Float32Array(dt_size * dt_size * 3)
      var length = dt_size * dt_size

      for (var i = 0; i < length; i++) {
        var val = THREE.Math.randFloat(0, 1)
        data_arr[i * 3 + 0] = val
        data_arr[i * 3 + 1] = val
        data_arr[i * 3 + 2] = val
      }

      var texture = new THREE.DataTexture(
        data_arr,
        dt_size,
        dt_size,
        THREE.RGBFormat,
        THREE.FloatType
      )
      texture.needsUpdate = true
      return texture
    },
  }
)

/**
 * @author alteredq / http://alteredqualia.com/
 */

THREE.MaskPass = function(scene, camera) {
  THREE.Pass.call(this)

  this.scene = scene
  this.camera = camera

  this.clear = true
  this.needsSwap = false

  this.inverse = false
}

THREE.MaskPass.prototype = Object.assign(Object.create(THREE.Pass.prototype), {
  constructor: THREE.MaskPass,

  render: function(renderer, writeBuffer, readBuffer, delta, maskActive) {
    var context = renderer.context
    var state = renderer.state

    // don't update color or depth

    state.buffers.color.setMask(false)
    state.buffers.depth.setMask(false)

    // lock buffers

    state.buffers.color.setLocked(true)
    state.buffers.depth.setLocked(true)

    // set up stencil

    var writeValue, clearValue

    if (this.inverse) {
      writeValue = 0
      clearValue = 1
    } else {
      writeValue = 1
      clearValue = 0
    }

    state.buffers.stencil.setTest(true)
    state.buffers.stencil.setOp(
      context.REPLACE,
      context.REPLACE,
      context.REPLACE
    )
    state.buffers.stencil.setFunc(context.ALWAYS, writeValue, 0xffffffff)
    state.buffers.stencil.setClear(clearValue)

    // draw into the stencil buffer

    renderer.render(this.scene, this.camera, readBuffer, this.clear)
    renderer.render(this.scene, this.camera, writeBuffer, this.clear)

    // unlock color and depth buffer for subsequent rendering

    state.buffers.color.setLocked(false)
    state.buffers.depth.setLocked(false)

    // only render where stencil is set to 1

    state.buffers.stencil.setFunc(context.EQUAL, 1, 0xffffffff) // draw if == 1
    state.buffers.stencil.setOp(context.KEEP, context.KEEP, context.KEEP)
  },
})

THREE.ClearMaskPass = function() {
  THREE.Pass.call(this)

  this.needsSwap = false
}

THREE.ClearMaskPass.prototype = Object.create(THREE.Pass.prototype)

Object.assign(THREE.ClearMaskPass.prototype, {
  render: function(renderer, writeBuffer, readBuffer, delta, maskActive) {
    renderer.state.buffers.stencil.setTest(false)
  },
})

/**
 * @author alteredq / http://alteredqualia.com/
 */

THREE.ShaderPass = function(shader, textureID) {
  THREE.Pass.call(this)

  this.textureID = textureID !== undefined ? textureID : 'tDiffuse'

  if (shader instanceof THREE.ShaderMaterial) {
    this.uniforms = shader.uniforms

    this.material = shader
  } else if (shader) {
    this.uniforms = THREE.UniformsUtils.clone(shader.uniforms)

    this.material = new THREE.ShaderMaterial({
      defines: Object.assign({}, shader.defines),
      uniforms: this.uniforms,
      vertexShader: shader.vertexShader,
      fragmentShader: shader.fragmentShader,
    })
  }

  this.camera = new THREE.OrthographicCamera(-1, 1, 1, -1, 0, 1)
  this.scene = new THREE.Scene()

  this.quad = new THREE.Mesh(new THREE.PlaneBufferGeometry(2, 2), null)
  this.quad.frustumCulled = false // Avoid getting clipped
  this.scene.add(this.quad)
}

THREE.ShaderPass.prototype = Object.assign(
  Object.create(THREE.Pass.prototype),
  {
    constructor: THREE.ShaderPass,

    render: function(renderer, writeBuffer, readBuffer, delta, maskActive) {
      if (this.uniforms[this.textureID]) {
        this.uniforms[this.textureID].value = readBuffer.texture
      }

      this.quad.material = this.material

      if (this.renderToScreen) {
        renderer.render(this.scene, this.camera)
      } else {
        renderer.render(this.scene, this.camera, writeBuffer, this.clear)
      }
    },
  }
)

var angle = 0
var radius = 4

var mouseX = 0,
  mouseY = 0

var windowHalfX = window.innerWidth / 2
var windowHalfY = window.innerHeight / 2

let ambientLight, spotLight, composer, glitchPass

const getRandomColour = () => Math.floor(Math.random() * 16777215).toString(16)

class Weight extends Component {
  componentDidMount() {
    const start = () => {
      if (!this.frameId) {
        this.frameId = requestAnimationFrame(this.animate)
      }
    }
    const stop = () => {
      cancelAnimationFrame(this.frameId)
    }
    const self = this
    const width = this.mount.clientWidth
    const height = this.mount.clientHeight

    //ADD SCENE
    this.scene = new THREE.Scene()

    //ADD CAMERA
    this.camera = new THREE.PerspectiveCamera(
      45,
      window.innerWidth / window.innerHeight,
      0.3,
      40
    )
    this.camera.position.set(0, 1, 3)

    ambientLight = new THREE.AmbientLight(0x404040, 15)
    this.scene.add(ambientLight)

    spotLight = new THREE.SpotLight(0x404040, 50)
    spotLight.position.set(-1, 3, 4)
    spotLight.castShadow = true

    spotLight.shadow.mapSize.width = 5
    spotLight.shadow.mapSize.height = 5

    spotLight.shadow.camera.near = 0.1
    spotLight.shadow.camera.far = 2000
    spotLight.shadow.camera.fov = 45

    this.scene.add(spotLight)

    // var axesHelper = new THREE.AxesHelper( 5 );
    // this.scene.add( axesHelper );

    const manager = new THREE.LoadingManager()
    manager.onProgress = function(item, loaded, total) {
      console.log(item, loaded, total)
    }

    const onProgress = function(xhr) {
      if (xhr.lengthComputable) {
        var percentComplete = (xhr.loaded / xhr.total) * 100
        if (Math.round(percentComplete, 2) === 100) {
          document
            .querySelector('.content__section__threed-text--load')
            .classList.add('content__section__threed-text--load--hide')
        }
      }
    }

    const onError = function(xhr) {
      console.log(xhr)
    }

    document.addEventListener('mousemove', onDocumentMouseMove, false)
    window.addEventListener('resize', onWindowResize, false)

    function onWindowResize() {
      windowHalfX = window.innerWidth / 2
      windowHalfY = window.innerHeight / 2

      self.camera.aspect = window.innerWidth / window.innerHeight
      self.camera.updateProjectionMatrix()

      self.renderer.setSize(window.innerWidth, window.innerHeight)
    }

    function onDocumentMouseMove(event) {
      mouseX = (event.clientX - windowHalfX) / 2
      mouseY = (event.clientY - windowHalfY) / 2
    }

    var mtlLoader = new MTLLoader(),
      objLoader = new OBJLoader()

    mtlLoader.load('./models/weight.mtl', materials => {
      materials.preload()
      objLoader.setMaterials(materials)
      objLoader.load('./models/weight.obj', object => {
        object.castShadow = true
        object.reciveShadow = true
        object.position.y = -0.5
        object.scale.set(0.4, 0.4, 0.4)
        self.scene.add(object)
        document
          .getElementById('AW__index__weight__Loader')
          .classList.add('AW__index__weight__Loader__fade')
        setTimeout(() => {
          let elem = document.getElementById('AW__index__weight__Loader')
          elem.parentNode.removeChild(elem)
        }, 300)
      })
    })

    // var geometry = new THREE.CircleBufferGeometry( 0.14, 100 );
    // var material = new THREE.MeshBasicMaterial( { color: 0xe0efee, opacity: 0.7} );
    // material.transparent = true;
    // var circle = new THREE.Mesh( geometry, material );
    // circle.position.z = 0.465;
    // circle.position.y = -0.11;
    // circle.rotation.x = -0.3;
    // this.scene.add( circle );

    this.renderer = new THREE.WebGLRenderer({ alpha: true })
    this.renderer.setClearColor('#000000', 0)
    this.renderer.setSize(width, height)
    this.mount.appendChild(this.renderer.domElement)

    composer = new THREE.EffectComposer(this.renderer)
    composer.addPass(new THREE.RenderPass(this.scene, this.camera))

    glitchPass = new THREE.GlitchPass()
    glitchPass.renderToScreen = true
    composer.addPass(glitchPass)

    start()

    setTimeout(() => {
      const paint = basicScroll.create({
        elem: document.querySelector('.AW__index__logo'),
        from: 'top-top',
        to: 'middle-middle',
        props: {
          '--opacityPaint': {
            from: 1,
            to: 0,
          },
        },
      })
      paint.start()

      const weight = basicScroll.create({
        elem: document.querySelector('.AW__index__logo'),
        from: 'top-top',
        to: 'middle-middle',
        props: {
          '--opacityWeight': {
            from: 1,
            to: 0,
          },
        },
      })
      weight.start()
    }, 1000)
  }
  componentWillUnmount() {
    // this.stop()
    this.mount.removeChild(this.renderer.domElement)
  }
  changeColor = () => {
    glitchPass.goWild = true
    setTimeout(() => {
      spotLight.color.setHex('0x' + getRandomColour())
      ambientLight.color.setHex('0x' + getRandomColour())
    }, 300)
    setTimeout(() => {
      glitchPass.goWild = false
    }, 600)
  }
  animate = () => {
    // looks weird as light moves to
    // make objects dragable

    this.camera.position.x += (mouseX - this.camera.position.x) * 0.05
    this.camera.position.y += (-mouseY - this.camera.position.y) * 0.05
    this.camera.position.x = radius * Math.cos(angle)
    this.camera.position.z = radius * Math.sin(angle)

    angle += 0.04

    this.camera.lookAt(this.scene.position)

    this.renderScene()
    this.frameId = requestAnimationFrame(this.animate)
  }
  renderScene = () => {
    composer.render()
    // this.renderer.render(this.scene, this.camera)
  }
  render() {
    let canvanMarginLeft
    if (this.props.pdp) {
      canvanMarginLeft = window.innerWidth < 992 ? 50 : 90
    } else {
      canvanMarginLeft = 0
    }

    const canvasWidth = window.innerWidth - canvanMarginLeft
    return (
      <div onClick={this.changeColor}>
        <div
          id="AW__index__weight__Loader"
          className="AW__index__weight__Loader"
        />
        <div className="AW__index__weight_background" />
        <div
          onClick={this.changeColor}
          style={{
            width: canvasWidth,
            height: window.innerHeight,
            marginLeft: canvanMarginLeft,
            position: 'fixed',
            'z-index': '-1',
          }}
          ref={mount => {
            this.mount = mount
          }}
        />
      </div>
    )
  }
}

export default Weight
